.bg{
  background-color: var(--bg-clr);
  width: 100%;
}
.light-theme{
  --bg-clr: #EEF5FF;
  --ul-clr:black;
  --bg-new:#EEF5FF;
  --bg-offhead:white;
  --bg-img:url('../src/Assets/light.png');
  --border-:1px solid black;
}
.dark-theme{
  --bg-clr: black;
 color: white !important;
 background-color: #1A102D;
  --ul-clr:white;
  --drop-:#0e1119;
  --bg-new:black;
  --bg-offhead:#1A102D;
  --bg-img:url('../src/Assets/dark.png');
  --border-:1px solid white;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
   
    -webkit-appearance: none;
    margin: 0; 
}

input[type=number] {
    -moz-appearance:textfield;
}
.btn_connect_icon_bg {
  left: -8px;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 2px 0px 0px 2px;
  background: linear-gradient(267.92deg, #00fff0 -8.04%, #01b5c0 69.56%);
  box-shadow: 0px 0px 11px 0px rgba(0, 0, 0, 0.24);
}
.btn_connection_wallet {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 13px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
}





.dropdown-menu-liqudity{
  background-color: #1A102D;
  
}

.dropdown-item-liqudity{
  color: #01f7f7;
}
.dropdown-item-liqudity:hover{
  background: #FD0093;
  color: #fff;
}

.button_trade {
  width: 170px;
  height: 55px;
  font-size: 20px;
  background: #00d1ff;
  border: none;
  color: #1d1132;
  font-weight: 500;
  font-family: "poppins";
}

.button_trade:hover {
  background: #FD0093;
  color: #fff;
}

.button_Learn, .connect_button {
  border: #00d1ff 1px solid;
  color: #00d1ff;
  width: 170px;
  height: 55px;
  font-size: 20px;
  background: none;
}

.button_Learn:hover {
  background: #00d1ff;
  color: #fff;
  border: none;
}
.connect_button {
  background-color: #00d1ff;
  color: #010017;
  width: 100%;
  height: 50px;
  font-size: 16px;
  font-weight: 600;
Line-height:16px;
border-radius: 10px;
border:none;


}
.button_connect{
  background-color: #00d1ff;
  color: #010017;
  width: 150px;
  height: 60px;
  font-size: 16px;
  font-weight: 600;
Line-height:16px;
border-radius: 10px;
border: none;
}
.button_connect_pink{
  background-color: #FD0093;
  color: #010017;
  width: 150px;
  height: 60px;
  font-size: 16px;
  font-weight: 600;
Line-height:16px;
border-radius: 10px;
border: none;
}
.form-dropdown{
  border: 1px solid #FD0093 !important;
  background-color: #010017 !important;
