html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  width: 10px;
  scroll-behavior: smooth;
}


 
/* Handle */
::-webkit-scrollbar-thumb {
  /* background: #FFFFFF; 
   */
   /* background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgb(46 80 87) 54%, rgba(9,9,121,1) 81%); */
   background-color: #06335f;
  border-radius: 10px;
  /* height: 20px !important; */
  scroll-behavior: smooth;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgb(89 19 130) 54%, rgba(9,9,121,1) 81%);
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1{
  font-size: 30px !important;
}
h2{
  font-size: 20px !important;
}
h3{
  font-size: 15px !important;
}
p{
  font-size: 15px !important;
}
h5{
  color: var(--ul-clr);
}