.dropdown {
  padding: 10px;
  position: relative;
  display: flex;
  gap: 10px;
  font-size: 23px !important;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 60px;
  left: 10px;
  /* border: 1px solid #efefef; */
  border-radius: 0.4em;
  /* min-width: 220px; */
  /* background: var(--bg-new) !important; */
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  padding: 12px 12px;
  font-family: "Poppins" !important;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  min-width: 220px;
  z-index: 1;
  color: #000000;
  background-color: var(--drop-);
  border: 1px solid #4C4F5F;
}
